.tabs {
  max-width: 100%;
}

.tabs__controls {
  position: relative;
  display: flex;
  justify-content: center;
  column-gap: 47px;
  margin: 0 auto 38px;
  width: fit-content;
}
.tabs__controls::after {
  content: "";
  position: absolute;
  top: 41px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #787878;
  opacity: 0.2;
}
@media screen and (max-width: 767px) {
  .tabs__controls {
    flex-wrap: wrap;
  }
}

.tabs__control {
  text-transform: lowercase;
  cursor: pointer;
  opacity: 0.5;
  min-width: auto;
  justify-content: center;
  border: none;
  border-bottom: 2px solid transparent;
  color: #102654;
  font-size: 20px;
  line-height: 30px;
  font-weight: 400;
  letter-spacing: 0;
  padding: 0 0 11px 0;
}
@media screen and (max-width: 1023px) {
  .tabs__control {
    margin: 0 10px;
    padding: 15px;
  }
}
@media screen and (max-width: 767px) {
  .tabs__control {
    margin: 10px 2% 0;
    padding: 10px;
    width: 45%;
  }
}
.tabs__control.is-active {
  opacity: 1;
  pointer-events: none;
}

.tabs__content {
  margin-bottom: 40px;
  transition: height 0.3s;
}

.tabs__element {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  padding: 0;
  margin: 0;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .tabs__element {
    padding: 15px;
  }
}

.tabs--unset .tabs__content {
  height: 100px;
}

.tabs--unset .tabs__element {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

[data-tabs=parent] {
  overflow: hidden;
}
[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

